<template>
    <yd-layout>
        <div class="applyForm">
            <div class="header">
                <span class="h_ch" :class="{h_active:langCh==true}" @click="langCh=!langCh">中文</span>
                <span class="h_zh" :class="{h_active:langZh==true}" @click="langZh=!langZh">英文</span>
            </div>
            <div class="useInput">
                <div class="title">
                    在职证明用途填写
                </div>
                <trsTextarea class="trsTextarea" placeholder="若用途为旅游，请务必在“选择出行日期”中填写出行时间。"></trsTextarea>
            </div>
            <div class="useInput" v-show="langCh==true">
                <div class="title">
                    在职证明抬头填写
                </div>
                <trsTextarea class="trsTextarea" placeholder="指该证明给哪个机构"></trsTextarea>
            </div>
            <div class="useInput useInput2" v-show="langZh==true">
                <div class="title">
                    在职证明抬头填写（英文）
                </div>
                <trsTextarea class="trsTextarea" placeholder="指该证明给哪个机构（英文）"></trsTextarea>
            </div>
            <div class="sleectType" v-on:click="statusPopup = true">
                <div class="type">选择收入类型</div>
                <div class="please">请选择</div>
            </div>
            <div class="selectTime">
                <div class="starttime">
                    <div class="st_left">开始时间</div>
                    <div class="st_right" @click="openStarttime()">{{formatDate(defaultDate, 'yyyy')}}年{{formatDate(defaultDate, 'MM')}}月{{formatDate(defaultDate, 'dd')}}日</div>
                </div>
                <div class="starttime endtime">
                    <div class="st_left">结束时间</div>
                    <div class="st_right" @click="openEndtime()">{{formatDate(defaultDate, 'yyyy')}}年{{formatDate(defaultDate, 'MM')}}月{{formatDate(defaultDate, 'dd')}}日</div>
                </div>
                <yd-datetime ref="starttime" class="monthDate" v-model="defaultDate" type="date" v-show="false" slot="right"></yd-datetime>
                <yd-datetime ref="endtime" class="monthDate" v-model="defaultDate" type="date" v-show="false" slot="right"></yd-datetime>
            </div>
            <div class="holiday">今年年休假剩余天数：16</div>
            <div class="useInput">
                <div class="title">
                    备注
                </div>
                <trsTextarea class="trsTextarea" placeholder="若在职证明有特殊需求，请在此说明"></trsTextarea>
            </div>
            <div class="mainContentFluid">
                <div class="approval">
                    审批人
                </div>
                <div class="fluidWidth">
                    <div class="tripAppro">
                        <div class="name_circle">
                            <ul>
                                <li>
                                    <div class="lastname" v-bind:class="{'check':leaderNum == 1}" v-on:click="leaderNum=1">
                                        廖
                                        <div class="nameClick">
                                        </div>
                                        <div class="imgClick">
                                        </div>
                                    </div>
                                    <div class="name">
                                        廖雯
                                    </div>
                                </li>
                            </ul>
                            <div class="clear"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false;">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-bind:class="{active:statusRadio==1}" v-on:click="statusRadio=1">类型1
                    </li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" v-show="statusPopup==false">
            <div class="agree">送领导审批</div>
            <div class="back" @click="$router.push('/proofofEmp')">取消</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
export default {
    name: 'applyForm',
    data() {
        return {
            leaderNum: -1,
            statusPopup: false,
            statusRadio: -1,
            defaultDate: '',
            langCh: true,
            langZh: false
        }
    },

    mounted() {
        document.setTitle('快捷审批流程');
        let vm = this;
        vm.defaultDate = vm.formatDate(new Date(), 'yyyy-MM-dd');
    },
    methods: {
        openStarttime() {
            this.$refs.starttime.open();
        },
        openEndtime() {
            this.$refs.endtime.open();
        }
    }
}
</script>
<style lang="less" scoped>
@import 'proofofEmp.less';
</style>


